.wrapper {
  height: calc(100vh - 44px);
  background: #fff;
  color: #333;
}
.wrapper .tab-item {
  position: relative;
}
.wrapper .albums,
.wrapper .mvs {
  padding-bottom: 20px;
}
.wrapper .albums li,
.wrapper .mvs li {
  display: flex;
  align-items: center;
  margin: 5px;
  color: #666;
}
.wrapper .albums li .left,
.wrapper .mvs li .left {
  position: relative;
  margin-right: 10px;
}
.wrapper .albums li .left img,
.wrapper .mvs li .left img {
  display: block;
  height: 60px;
  width: 60px;
  object-fit: cover;
}
.wrapper .albums li .left .playCount,
.wrapper .mvs li .left .playCount {
  position: absolute;
  right: 5px;
  top: 5px;
  color: #fff;
  font-size: 12px;
  font-weight: 700;
}
.wrapper .albums li .right,
.wrapper .mvs li .right {
  flex-grow: 1;
  overflow: hidden;
}
.wrapper .albums li .right .title,
.wrapper .mvs li .right .title {
  font-size: 18px;
  margin-bottom: 5px;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.wrapper .info-box {
  padding: 10px 20px;
  line-height: 1.3;
}
.wrapper .info-box .title {
  font-size: 16px;
  color: #000;
  border-left: 2px solid #108ee9;
  padding-left: 5px;
  line-height: 1;
  margin-bottom: 5px;
}
.wrapper .info-box .text {
  margin-bottom: 20px;
  line-height: 1.5;
}
