.container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: #fff;
  z-index: 99;
}
.container .wrapper {
  overflow: scroll;
  height: 100%;
  width: 100%;
}
.container .wrapper .singer-img {
  position: fixed;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 250px;
  background-size: cover;
  background-repeat: no-repeat;
}
.container .wrapper .singer-img:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: hsla(0, 0%, 4%, 0.2);
}
.container .wrapper .content-box {
  margin-top: 250px;
}
